        Opal.const_set($nesting[0], 'TABLES', $hash2(["PNM", "PNF", "ENM", "ENF", "NNM", "NNF", "RNM", "RNF", "BN1", "BN2", "TN1", "TN2", "MTV", "HIT", "GCC", "WCC", "EVC", "BSD", "IDI"], {"PNM": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：ピグマー族（男）", "1D10", ["バビロン", "グリニッジ", "デトロイト", "ヨコスカ", "ボルドー", "テキサス", "シチリア", "チェルノブイリ", "グンマ", "サマルトリア"]), "PNF": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：ピグマー族（女）", "1D10", ["ルアンダ", "ローマ", "フロリダ", "ホノルル", "ツガル", "ゲルニカ", "シャンハイ", "モナコ", "チグリス", "オーサカ"]), "ENM": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：エレメント族（男）", "1D10", ["アポロン", "ミキストリ", "アザゼル", "フマクト", "マサカド", "ククルカン", "ルシフェル", "ザギグ", "フェムト", "マイトレーヤ"]), "ENF": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：エレメント族（女）", "1D10", ["クシナダ", "アルテミス", "ゼノビア", "フレイヤ", "イシュタム", "ベルゼバブ", "マイシェラ", "バステト", "スクルド", "アテナ"]), "NNM": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：ノーマッド族（男）", "1D10", ["ドラム", "カホン", "ハレルヤ", "トリノウタ", "スリラー", "シンバル", "リュート", "ウクレレ", "タンバリン", "ユメコウネン"]), "NNF": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：ノーマッド族（女）", "1D10", ["ピアノ", "テルミン", "ソバカス", "イマジン", "ツナミ", "ピッコロ", "ハープ", "シャミセン", "ミザルー", "ドナドナ"]), "RNM": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：ラット族（男）", "1D10", ["ポチ", "シシマル", "ポンタ", "コテツ", "アルフォンス", "パトラッシュ", "ミッキー", "ジジ", "サカモト", "オンソクマル"]), "RNF": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：ラット族（女）", "1D10", ["タマ", "ココ", "ラブ", "ピーコ", "モカ", "オリガミ", "ヒメ", "ミィ", "ルナ", "ク・メル"]), "BN1": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：ブレイン族（その１）", "1D10", ["マリファナ", "バファリン", "タミフル", "セーロガン", "モルヒネ", "ハルシオン", "トリカブト", "バイアグラ", "エリクサー", "クラレ"]), "BN2": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：ブレイン族（その２）", "1D10", ["ニトロ", "ダイオキシン", "タウリン", "コイーバ", "マールボロ", "キャメル", "ドクダミ", "アブサン", "ドブロク", "マティーニ"]), "TN1": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：テンタクル族（その１）", "1D10", ["アップル", "プリン", "ビフテキ", "ガンモ", "レバニラ", "カボチャ", "コロッケ", "マトン", "ギョーザ", "タバスコ"]), "TN2": $$$($$($nesting, 'DiceTable'), 'Table').$new("名前表：テンタクル族（その２）", "1D10", ["キノコ", "セロリ", "ラザニア", "ユドーフ", "ニンジン", "カイワレ", "ボルシチ", "ハクサイ", "キャラメル", "ワタアメ"]), "MTV": $$$($$($nesting, 'DiceTable'), 'Table').$new("動機決定表", "1D10", ["金。お宝の臭いがした。", "正義。破界の塔は災いのもと。絶たねばならない。", "友情。この破界の塔のせいで友人が困っている。助けなくちゃ。", "探究心。破界の塔のことをもっと知りたい。", "戦闘狂。もっと戦いたい。", "暇つぶし。退屈な日常を忘れたい。", "自殺願望。なんかもう死にたい。", "冒険家。ワクワクしたい。", "山男。シティが肌に合わない。", "特に動機らしい動機はない。"]), "HIT": $$$($$($nesting, 'DiceTable'), 'Table').$new("部位決定チャート", "1D10", ["コックピット", "エンジン", "フレーム", "フレーム", "フレーム", "フレーム", "ライトアーム", "レフトアーム", "ライトレッグ", "レフトレッグ"]), "GCC": $$$($$($nesting, 'DiceTable'), 'Table').$new("ガラコ改造チャート表", "1D10", ["【命中+】価格+200。【操作性】+1。［不安定］1。", "【回避+】価格+200。【機動性】+1。［不安定］1。", "【視界+】価格+200。【視認性】+2。［不安定］1。", "【移動+】価格+100。【移動力】+1。", "【火力+】価格+200。その部位に装着した武器の火力を常に+2する。", "【部位装甲+】価格+100。【部位装甲】+2。", "【限界重量+】価格+100。【限界重量】+1000。", "【安定性+】価格+50。［不安定］-1。", "【スロット+】価格+500。【スロット】+1。", "【弱体無効】価格+500。このパーツへの部位ダメージによる[弱体]の効果を無視する。"]), "WCC": $$$($$($nesting, 'DiceTable'), 'Table').$new("武器改造チャート表", "1D10", ["【命中+】価格+200。【操作性】+1。", "【火力+】価格+200。【火力】+2。", "【射程】価格+200。【射程】+3。「射程：近接」の場合、「射程:3 or 近接」となる(攻撃する度にどちらかを選ぶ)。", "【範囲+】価格+200。1シーンにつき1回、この武器の目標を「範囲2」に変更してもよい(フリーアクション)。もともと範囲攻撃できる武器の場合は、「範囲n+1」にできる(1シーン1回、フリーアクション)。", "【部位変更】価格+200。装着できる部位がランダムに追加される。部位決定チャート(『GHT』p21)を使用して決めること。", "【部位装甲+】価格+100。装着した部位の【部位装甲】+2。", "【精度+】価格+100。この武器を使って狙い撃ちをする場合、命中判定に+1。", "【装飾+】価格+500。特に効果はないが、売却した時の金額が上昇する。", "【幸運+】価格+500。この武器による命中判定の出目が1だった場合、判定を振り直しても良い(1シーン1回まで)。", "【回数無限】価格+500。武器の使用回数制限がなくなる。"]), "EVC": $$$($$($nesting, 'DiceTable'), 'Table').$new("イベントチャート表", "1D10", ["【クリーチャー】スタートル(『GTD』p30)が1d10+3体現れる。戦闘開始。", "【ビット】コーンノーズ(『GTD』p23)が1d10+3体現れる。戦闘開始。", "【ノーマッド】ノーマッド族のランドクローラーと遭遇する。このシーンはノーマッドからアイテムを購入しても良い。ノーマッド族は天蓋都市で購入できるすべてのアイテムを販売している(ただし金額は20%増し)。", "【ピグマー族】君達の目的地方面から、ボロボロになったピグマー族のNPCが歩いてくる。NPCに何があったのかはGMが決めよ。ピグマー族を天蓋都市まで送った場合、謝礼として200クレジットを受け取ることが出来る。NPCは重量50のアイテムとして扱う。", "【ビット】ダスクウォッチ(『GTD』p23)が1d10+3体現れる。戦闘開始。", "【異常気象】嵐、竜巻、豪雨など、異常な気象によって行動を阻害される。PCのうち代表者1名が【視認】10の判定を行うこと。失敗した場合、次のシーンはスポットを移動できない。現在のスポットに留まることになる。", "【クリーチャー】ナグ(『GTD』p31)が1d10+4体現れる。戦闘開始。", "【ビット】ランオーバー(『GTD』p25)が3体現れる。戦闘開始。", "【猛毒の霧】付近に毒の霧が立ち込める。全てのキャラクターは毒によって1d10のHPダメージを受ける。", "【最悪の敵】ズルワーン(『GTD』p29)が1体現れる。戦闘開始。"]), "BSD": $$$($$($nesting, 'DiceTable'), 'Table').$new("戦闘開始距離", "1D10", ["3マス", "3マス", "6マス", "6マス", "9マス", "9マス", "12マス", "12マス", "15マス", "15マス"]), "IDI": $$$($$($nesting, 'DiceTable'), 'RangeTable').$new("個性表", "1D100", [[$range(1, 5, false), "〈近接武器熟練〉 近接攻撃の火力+1。"], [$range(6, 10, false), "〈遠隔武器熟練〉 遠隔攻撃の火力+1。"], [$range(11, 15, false), "〈天才〉 【技術】+1。"], [$range(16, 20, false), "〈頑強〉 【身体】+1。"], [$range(21, 25, false), "〈早業〉 【速度】+1。"], [$range(26, 30, false), "〈スイフトフット〉 【移動力】+1。"], [$range(31, 35, false), "〈超反応〉 行動判定値+2。"], [$range(36, 40, false), "〈警戒心〉 罠を発見するための判定に+2。"], [$range(41, 45, false), "〈解除屋〉 罠を解除するための判定に+2。"], [$range(46, 50, false), "〈タフガイ〉 最大HP+5。"], [51, "〈踏み込み〉 キミが使用する近接武器のデータを「射程：2」に変更する。"], [52, "〈不動〉 キミは強制移動の効果を受けない。"], [53, "〈ペイローダー〉 ガラコの【限界重量】+2000。"], [54, "〈魅力〉 キミがHPを回復するアイテム、もしくは超能力の目標になった時、キミのHPを追加で1点回復する。"], [55, "〈ダブルタップ〉 キミのターン開始時に使用。このターンの間、キミは追加で1回の遠隔攻撃を行うことができる。"], [56, "〈薙ぎ払い〉 キミのターン開始時に使用。このターンの間、キミが行う近接攻撃の目標を「周囲1マス以内にいるすべての敵」に変更する。"], [57, "〈武器落とし〉 キミは部位ひとつを指定する。目標は指定された部位に（スロットを消費して）装着している武器すべてを地面に落とす。"], [58, "〈切り払い〉 キミが行う回避判定の直前に使用。その判定を、【機動性】ではなく【操作性】で判定してよい。ただし、キミは近接武器を装着していなければならない。"], [59, "〈体崩しの達人〉 キミが目標のレッグに攻撃を命中させる度、その目標は【機動性】10の判定を行う。失敗した場合、目標は［伏せ］状態になる。"], [60, "〈超分解術〉 アイテムひとつを目標にする。目標のアイテムの重量を1/4にする。ただし、そのアイテムは使用できなくなる。再度〈超分解術〉の判定に成功することで、元に戻せる（重量が元に戻り、アイテムが使用可能になる）。"], [61, "〈即時換装〉 キミは、ガラコのパーツ換装を（ベースアクションではなく）ムーブアクションで行ってもよい。"], [62, "〈ノックバック〉 キミが目標に5点以上の最終ダメージを与えた直後に使用。目標を1マス、任意の方向に強制移動させる。近接武器で攻撃した場合のみ使用できる。"], [63, "〈照準〉 このターンの間、次に行う攻撃の命中判定+1。"], [64, "〈燃料節約術〉 戦闘時以外、キミは燃料を消費しなくてよい。"], [65, "〈追撃〉 キミの敵が、隣接するマスから離れるような移動を宣言した直後に使用。キミはその敵に対して近接攻撃を行う。近接攻撃の後、敵は移動を行うこと。"], [66, "〈連撃〉 キミが敵の部位を［修復不能］にした直後に使用。キミは再度、その敵に対して攻撃を行う。"], [67, "〈殺し屋〉 キミがコックピットに攻撃を命中させる度、そのガラコの操縦者は2点のHPを失う。"], [68, "〈極大射程〉 キミが扱う遠隔武器の射程を2倍にする。"], [69, "〈援護射撃〉 目標が回避判定を行った直後に使用。目標の回避判定の達成値-1。その後、キミは準備済みの遠隔武器ひとつの使用回数を1減らすこと。"], [70, "〈鉄壁〉 キミがダメージを受けた直後に使用。そのダメージを無効化する。"], [71, "〈心臓狙い〉 キミが部位狙いを行い、コックピット、もしくはエンジンに対して攻撃を行う際、命中判定+1。"], [72, "〈四肢狙い〉 キミが部位狙いを行い、アーム、もしくはレッグに対して攻撃を行う際、命中判定+1。"], [73, "〈窮地逆転〉 キミの判定の出目が1だった時、その出目を10に変更する。"], [74, "〈防御重視〉 ラウンド開始時に使用。【操作性】-1。【機動性】+2。ラウンド終了時まで。"], [75, "〈チアガール〉 目標は即座に追加のターンを得る。"], [76, "〈毒半減〉 キミが［毒］状態になった時、毎回失うHPを1点減らす。ノーマッド族はこの個性を取得できない。"], [77, "〈毒無効〉 キミは［毒］状態にならない。この個性はノーマッド族だけが取得できる。"], [78, "〈生存術〉 キミは各シーン終了時、HPを減らさなくてよい。"], [79, "〈平衡感覚〉 キミは［不安定］状態のペナルティを受けない。"], [80, "〈不屈〉 キミのターン開始時に使用。このターンの間、キミはガラコの損傷による［弱体］の効果を受けない。"], [81, "〈プレデターセンス〉 近接攻撃の命中判定+2。この個性はラット族だけが取得できる。"], [82, "〈鷹の目〉 遠隔攻撃の命中判定+2。この個性はラット族だけが取得できる。"], [83, "〈超リペア術〉 部位をひとつ選択する。目標の部位の被ダメージすべてを一時的に回復する（修復不能を除く）。回復したダメージは、シーン終了時に元に戻る（再度壊れる）。この個性はブレイン族のみが取得できる。"], [84, "〈浮遊術〉 キミは［飛行］状態になる。シーン終了時まで。この個性はテンタクル人のみが取得できる。"], [85, "〈瞬間移動術〉 キミは任意のマスに瞬間移動する。この個性はテンタクル人のみが取得できる。"], [86, "〈ハイボルテージ〉 4ラウンドめ以降、キミが持つすべての武器の火力を+2する。"], [87, "〈スライドディフェンス〉 キミが部位決定チャートを振った直後に使用。チャートの結果を+1する。"], [88, "〈カーブアタック〉 目標が部位決定チャートを振った直後に使用。チャートの結果を-1する。"], [89, "〈サイコショット〉 目標に［火力0］の攻撃を行う（自動命中）。（超能力）"], [90, "〈ファイア〉 目標に［火力5］の攻撃を行う。（超能力）"], [91, "〈アイス〉 目標に［火力3］の攻撃を行う。（超能力）"], [92, "〈サンダー〉 目標に［火力2］の攻撃を行う。（超能力）"], [93, "〈テレパシー〉 キミは念話によって会話することができる。（超能力）"], [94, "〈ミラー〉 目標が超能力の使用を宣言した直後に使用。超能力の目標を使用者自身に変更する。（超能力）"], [95, "〈バインド〉 目標のターン開始時に使用。目標の移動力-3。ターン終了時まで。（超能力）"], [96, "〈アーマー〉 目標のすべての部位装甲+2。シーン終了時まで。（超能力）"], [97, "〈バリア〉 目標がダメージを受けた直後に使用。ダメージを3点軽減する。（超能力）"], [98, "〈ヒール〉 目標のHPを［1d10-4］点回復する。出目が低いとHPを失う可能性があることに注意。（超能力）"], [99, "〈カース〉 目標が判定を行った直後に使用。その判定の達成値を-3する。"], [100, "〈リザレクション〉 死んだ目標を生き返らせる。生き返った目標のHPは10になる。このシーンの間に死亡したキャラクターのみ目標にできる。（超能力）"]])}).$freeze());
